import React from 'react';
import SubView from 'components/main/pages/home/views/SubView';

class CreateOrJoin extends SubView {

  constructor(props){
    super(props);
    this.init(props);
    this.state = { type: 'join' }
  }

  typeOptions(){
    const optionsStyle = {...this.bs, ...{
      width: '',
      height: '',
      justifyContent: 'center'
    }}
    return(
      <div style={optionsStyle}>
        {this.checkBox(this.func.multiLang('Join other group','',''), this.state.type === 'join', (e)=>{this.setState({ type: 'join' })}, 'black')}
        {this.gap(this.bs.height * 0.015)}
        {this.checkBox(this.func.multiLang('Create your own group','',''), this.state.type === 'create', (e)=>{this.setState({ type: 'create' })}, 'black')}
        {this.gap(this.bs.height * 0.015)}
      </div>
    )
  }

  render(){
    this.init(this.props);
    const join = this.state.type === 'join';

    if(this.func.outDated(this.store.projects.viewingProject.endDate)){
      return(
        <div style={this.subViewStyle()}>
          {this.gap('5%')}
          {this.subTitle(['This project is expired','',''])}
        </div>
      )
    }

    return(
      <div style={this.subViewStyle()}>
        {this.gap('5%')}
        {this.subTitle(['You are not belong to any group in this project','',''])}
        {this.sep()}
        {this.gap('2%')}
        {this.typeOptions()}
        {this.gap('2%')}

        {join && this.subTitle(['Join group','',''])}
        {join && this.sep()}
        {join && this.inputs.inputField('groupCode','text', ['Enter group code','',''], '')}
        {join && this.buttons.rectGreen(['Join group','',''], ()=>{ this.joinGroup(); } )}

        {!join && this.subTitle(['Create group','',''])}
        {!join && this.sep()}
        {!join && this.inputs.inputField('groupName','text', ['Enter group name','',''], '')}
        {!join && this.buttons.rectGreen(['Create group','',''], ()=>{ this.createGroup(); } )}
      </div>
    )
  }

  joinGroup(){
    const groupCode = document.getElementById('groupCode').value;
    if(groupCode.length < 5){
      return this.failedMessage(['Invalid group code!', '!', '!'])
    }
    this.actions.groups.joinGroup(this.store.user._id, groupCode);
  }

  createGroup(){
    const groupName = document.getElementById('groupName').value;
    if(groupName.length < 2){
      return this.failedMessage(['Group name must be at lease 2 characters long!', '!', '!'])
    }
    this.actions.groups.createGroup(this.store.user._id, this.store.projects.viewingProject._id, groupName);
  }

}

export default CreateOrJoin;



